import React, { useState } from 'react'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button';
import Signinimg from './Signinimg';
import { useNavigate } from 'react-router-dom'
import { NavLink } from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const Home = () => {

   const history = useNavigate();

    const [inpval, setInpval] = useState({
        name: "",
        dob: "",
        phone: "",
        dept: "",
        email: "",
        password: "",

    })
    const [data,setData] = useState([]);
    console.log(inpval);

    const getdata = (e) => {
        //console.log(e.target.value)
        const { value, name } = e.target;
        //console.log(value,name);

        setInpval(() => {
            return {
                ...inpval,
                [name]: value
            }

        })
    }
    const addData = (e) => {
        e.preventDefault();

        const { name, email, dob,phone,dept, password } = inpval;

        if (name === "") {
            toast.error(' name field is requred!',{
                position: "top-center",
            });
        }
           else if (dept === "") {
                toast.error(' dept field is requred!',{
                    position: "top-center",
                });
            }
        else if (email === "") {
             toast.error('email field is requred',{
                position: "top-center",
            });
        } else if (!email.includes("@")) {
             toast.error('plz enter valid email addres',{
                position: "top-center",
            });
        } else if (dob === "") {
             toast.error('date of birth field is requred',{
                position: "top-center",
            });
        } else if (password === "") {
             toast.error('password field is requred',{
                position: "top-center",
            });
        } else if (password.length < 5) {
             toast.error('password length greater five',{
                position: "top-center",
            });
        } else if (phone === "") {
            toast.error('password field is requred',{
               position: "top-center",
           });
       } else if (phone.length != 10) {
            toast.error('Enter valid Phone Number',{
               position: "top-center",
           });
        } else {
            console.log("data added succesfully");
            history("/login")
            localStorage.setItem("usersignupdata",JSON.stringify([...data,inpval]));

        }

    }

    return (
        <>
            <div className="container mt-3">
                <section className='d-flex justify-content-between'>
                    <div className="left_data mt-3 p-3" style={{ width: "100%" }}>
                        <h3 className='text-center col-lg-6'>Sign up</h3>
                        <Form>
                            <Form.Group className="mb-3 col-lg-6" controlId="formBasicName">
                                <Form.Control type="text" name="name" onChange={getdata} placeholder="Enter your Name" />
                            </Form.Group>
                            <Form.Group className="mb-3 col-lg-6" controlId="formBasicDob">
                                <Form.Control type="date" name="dob" onChange={getdata} placeholder="Enter your Date of Birth" />
                            </Form.Group>
                            <Form.Group className="mb-3 col-lg-6" controlId="formBasicPhone">
                                <Form.Control type="Number" name="phone" onChange={getdata} placeholder="Enter your Phone Number" />
                            </Form.Group>
                            <Form.Group className="mb-3 col-lg-6" controlId="formBasicDept">
                                <Form.Control type="text" name="dept" onChange={getdata} placeholder="Enter your Department" />
                            </Form.Group>
                            <Form.Group className="mb-3 col-lg-6" controlId="formBasicEmail">
                                <Form.Control type="email" name="email" onChange={getdata} placeholder="Enter your Email ID" />
                            </Form.Group>
                            <Form.Group className="mb-3 col-lg-6" controlId="formBasicPassword">
                                <Form.Control type="password" name="password" onChange={getdata} placeholder="Password" />
                            </Form.Group>
                            <Form.Group className="mb-3 col-lg-6" controlId="formBasicCheckbox">
                                <Form.Check type="checkbox" name="agree" label="I Agree, given info are correct" />
                            </Form.Group>
                            <Button variant="primary" className='col-lg-6' onClick={addData} style={{ background: "rgb(67, 185, 127)" }} type="submit">
                                Submit
                            </Button>
                        </Form>
                        <p className='mt-3'>Already Have an Account <span><NavLink to="/login">SignIn</NavLink></span> </p>
                    </div>
                    <Signinimg />
                </section>  
                <ToastContainer />
            </div>
        </>
    )
}

export default Home
